<template>
	<view>
		<view style="padding: 1rem;">
			<u-swiper :list="swiper"  indicatorMode="line"   previousMargin="30"
			        nextMargin="30"
			        circular
			        :autoplay="true"
			        radius="5"
					showTitle
					
			        bgColor="#ffffff"></u-swiper>
		</view>
		<view class="Imagetext">
			<u-grid :border="false" col="4">
				<u-grid-item v-for="(t,index) in imageRouting" >
					<u--image :showLoading="true" :src="t.src" width="32px" height="32px" :lazy-load="true"  mode="widthFix" ></u--image>
					<text style="font-weight: 300;">{{t.title}}</text>
				</u-grid-item>
			</u-grid>
		</view>
		<view class="News">
			<view class="figure" v-for="n in News" @click="goNewsdetail(n.id)">
					<view class="news-images">
						<u--image  :showLoading="true" :src="$url+n.imgUrl" width="100%" :lazy-load="true" shape="square" radius="5" height="100%"  mode="widthFix" ></u--image>
					</view>
					<view class="content">
						<u--text  class="title" :text="n.title" size="18"  :bold="true" lines="2"></u--text>
						<u--text :text="n.summary" margin="10px 0" lines="3"></u--text>
					</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				imageRouting:[
					{
						src:"/static/images/movie.png",
						title:'电影'
					},
					{
						src:'/static/images/food.png',
						title:"食物",
					},
					{
						src:'/static/images/diagnosis.png',
						title:'诊断'
					},
					{
						src:'/static/images/stop.png',
						title:"找车位"
					}
				],
				News:[],
				swiper:[],
			}
		},
		mounted() {
			this.getImages();
			this.getNews();
		},
		methods: {
			getImages(){
				this.$request.get("prod-api/api/volunteer-service/ad-banner/list")
				.then(res=>{
					if(res.code==200){
						res.data.forEach(item=>{
							
							this.swiper.push(this.$url+item.imgUrl);
						})
						
						
					}
				})
			},
			getNews(){
				this.$request.get("prod-api/api/volunteer-service/news/list")
				.then(res=>{
					if(res.code==200){
						
						this.News=res.rows;
					}else{
						
					}
				})
				
			},
			goNewsdetail(id){
				uni.navigateTo({
					url: '/pages/newsdetail/newsdetail?newsid='+id,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
</script>

<style lang="scss">
	
	.News{
		padding: .8rem;
		
		.figure{
			border-radius: 5px;
			margin: 1rem;
			background-color: #fff;
			font-size: .8rem;
			box-shadow:  2px 2px 5px  $u-info-disabled;
		}
		.content{
			padding: 1rem;
			color: red;
			.title{
				color: red;
				font-size: 50rpx;
				font-weight: 300;
				
			}
		}
		
	}
</style>
