<template>
	<view>
		<view style="padding: 1rem;">
			<view class="company-item">
				<u--text text="我用十年青春,赴你最后之约"></u--text>
				<view style="padding: 1rem;">
					<u--text text="我用十年青春,赴你最后之约"></u--text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				companyList:[]
			}
		},
		mounted() {
			this.getcompanyList();
		},
		methods:{
			getcompanyList(){
				this.$request.get('prod-api/api/job/company/list')
				.then(res=>{
					if(res.code==200){
						this.companyList=res.rows;
						
					}else{
						uni.showToast({
							title:res.msg
						});
					}
				})
				.catch(err=>{
					console.log(err);
				})
			}
		}
	}
</script>

<style lang="scss">
	.company-item{
		border-radius: 10rpx;
		background-color: #fff;
		
		padding: 2rem;
	}
</style>
